function calculate() {
  const numberInput = document.getElementById("numberInput");
  const planSelect = document.getElementById("planSelect");
  const phoneInput = document.getElementById("phoneInput");
  const resultDiv = document.getElementById("result");

  const number = Number(numberInput.value);
  const plan = Number(planSelect.value);
  const phone = phoneInput.value.trim();

  if (isNaN(number)) {
    resultDiv.innerText = "Будь ласка, введіть число";
    return;
  }

  if (!phone) {
    resultDiv.innerText = "Будь ласка, введіть номер телефону";
    return;
  }

  const result = number * plan;
  resultDiv.innerText = `Приблизна початкова вартість Вашої тераси становить від ${result} $`;

  clearForm();

  function clearForm() {
    const form = document.getElementById("formCalc");
    form.reset();
  }
}

// Натискання на заголовок і поява зкритого тексту

const titles = Array.from(document.querySelectorAll(".hidden-title"));
const contents = Array.from(document.querySelectorAll(".content"));
const buttons = Array.from(document.querySelectorAll(".hidden-btn"));

titles.forEach((title, index) => {
  const content = contents[index];
  const button = buttons[index];

  title.addEventListener("click", () => {
    titles.forEach((t, i) => {
      if (i !== index) {
        const c = contents[i];
        t.classList.remove("hidden");
        c.classList.add("hidden");
      }
    });

    content.classList.toggle("hidden");
    title.classList.toggle("hidden");
  });

  button.addEventListener("click", () => {
    content.classList.add("hidden");
    title.classList.remove("hidden");
  });
});

// поява та зникнення віконичка з подякою

const btn_pop = document.querySelectorAll(".my-button");

btn_pop.forEach((button) => {
  button.addEventListener("click", (event) => {
    const form = event.target.closest("form");
    const inputs = form.querySelectorAll("input, textarea");
    const inputsAreEmpty = Array.from(inputs).some(
      (input) => input.value === ""
    );

    if (!inputsAreEmpty) {
      const popup = document.querySelector(".popup");
      popup.style.display = "block";

      setTimeout(() => {
        var inputs = document.querySelectorAll(
          'input[type="text"], input[type="number"]'
        );
        // Проходимося по всіх елементах масиву і оновлюємо значення
        for (var i = 0; i < inputs.length; i++) {
          var updatedValue = "";
          inputs[i].value = updatedValue;
        }

        popup.style.display = "none";
      }, 4000);
    }
  });
});

// Ваша ідентифікаційна кількість YouTube
var videoId = "BNXq0KdIKtk";

// Створення вбудованого плеєра YouTube
var player;
function onYouTubeIframeAPIReady() {
  player = new YT.Player("player", {
    height: "360",
    width: "640",
    videoId: videoId,
    events: {
      onReady: onPlayerReady,
    },
  });
}

// При натисканні на відео, починаємо програвання
function onPlayerReady(event) {
  document.getElementById("player").addEventListener("click", function () {
    event.target.playVideo();
  });
}
